import React, { SyntheticEvent } from "react";
import images from "../../assets/images";
import { Accordion, Card } from "react-bootstrap";
function NearBySearches(): JSX.Element {
  const onSelect = (key: string | null, event: SyntheticEvent<unknown>) => {
    const target = event.currentTarget as HTMLDivElement;
    const card = target.closest<HTMLDivElement>(".card") as HTMLDivElement;
    const heading = card.querySelector<HTMLHeadingElement>(
      ".content_sub_heading"
    ) as HTMLHeadingElement;
    const materialIcon = card.querySelector(
      ".material-icons"
    ) as HTMLSpanElement;
    if (key === null) {
      materialIcon.innerText = "expand_more";
      card.classList.remove("mt-5");
      heading.classList.add("mb-0");
    } else {
      materialIcon.innerText = "expand_less";
      card.classList.add("mt-5");
      heading.classList.remove("mb-0");
    }
  };
  return (
    <>
      <Accordion onSelect={onSelect} defaultActiveKey="0">
        <Card className="mt-5">
          <Accordion.Toggle as={Card.Header} eventKey="0">
            <div className="card-header-inner" style={{ cursor: "pointer" }}>
              <h6
                className="content_sub_heading d-inline-flex justify-content-between align-items-center w-100"
                style={{ height: "50px" }}
              >
                Nearby Schools
                <span className="material-icons float-right arrow">
                  expand_less
                </span>
              </h6>
            </div>
          </Accordion.Toggle>
          <Accordion.Collapse eventKey="0">
            <Card.Body>
              <div className="nearby-searches">
                <div className="search_location w-100">
                  <img src={images.maps} alt="map" className="mb-3" />
                  <div className="location_distance">
                    <p className="mb-0">
                      0.1 <br />
                      mi
                    </p>
                  </div>
                  <div className="location">
                    <p className="location_name mb-0">
                      Ps 183 Robert L Stevenson
                    </p>
                    <div className="location-status">
                      <p className="mb-0 location_status_details">Public </p>
                      <p className="mb-0 location_status_details">
                        Grades PK-5{" "}
                      </p>
                    </div>
                  </div>
                  <div className="location_distance">
                    <p className="mb-0">
                      0.1 <br />
                      mi
                    </p>
                  </div>
                  <div className="location">
                    <p className="location_name mb-0">
                      Ps 183 Robert L Stevenson
                    </p>
                    <div className="location-status">
                      <p className="mb-0 location_status_details">Public </p>
                      <p className="mb-0 location_status_details">
                        Grades PK-5{" "}
                      </p>
                    </div>
                  </div>
                  <div className="location_distance">
                    <p className="mb-0">
                      0.1 <br />
                      mi
                    </p>
                  </div>
                  <div className="location pb-5">
                    <p className="location_name mb-0">
                      Ps 183 Robert L Stevenson
                    </p>
                    <div className="location-status">
                      <p className="mb-0 location_status_details">Public </p>
                      <p className="mb-0 location_status_details">
                        Grades PK-5{" "}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </Card.Body>
          </Accordion.Collapse>
        </Card>
      </Accordion>
    </>
  );
}
export default NearBySearches;
