import React, { MouseEvent } from "react";
function PropertyFeatures(): JSX.Element {
  const onClick = (event: MouseEvent) => {
    const target = event.currentTarget as HTMLDivElement;
    const parent = target.closest<HTMLDivElement>(
      ".property-features"
    ) as HTMLDivElement;
    const materialIcon = parent.querySelector<HTMLSpanElement>(
      ".material-icons"
    ) as HTMLSpanElement;
    const child = parent.querySelector<HTMLDivElement>(
      ".collapse-child"
    ) as HTMLDivElement;
    if (child.classList.contains("collapse")) {
      child.classList.remove("collapse");
      parent.style.removeProperty("border-bottom");
      materialIcon.innerText = "expand_less";
      target.classList.add("mb-4");
      target.classList.remove("mb-0");
      parent.classList.remove("pb-0");
    } else {
      child.classList.add("collapse");
      parent.style.borderBottom = "0px";
      materialIcon.innerText = "expand_more";
      target.classList.remove("mb-4");
      target.classList.add("mb-0");
      parent.classList.add("pb-0");
    }
  };
  return (
    <div className="property-features collapseable">
      <h6 className="content_sub_heading collapse-head mb-4" onClick={onClick}>
        <span>Property Features</span>
        <span className="material-icons">expand_less</span>
      </h6>
      <div
        className="d-flex flex-column collapse-child"
        style={{ width: "375px", maxWidth: "100%" }}
      >
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Price/Sq.Ft.</p>
          <p className="mb-0 property_feature_list_details">$725/Sq.Ft.</p>
        </div>
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Baths</p>
          <p className="mb-0 property_feature_list_details">3 Full, 1 parial</p>
        </div>
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Community</p>
          <p className="mb-0 property_feature_list_details" />
        </div>
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Stories</p>
          <p className="mb-0 property_feature_list_details">2</p>
        </div>
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Property Type</p>
          <p className="mb-0 property_feature_list_details">Multifamily</p>
        </div>
        <div className=" property_feature_heading_list d-inline-flex justify-content-between align-items-center w-100">
          <p className="mb-0">Community</p>
          <p className="mb-0 property_feature_list_details">Brooklyn</p>
        </div>
      </div>
    </div>
  );
}
export default PropertyFeatures;
