import React, { MouseEvent } from "react";
import images from "../../assets/images";
import { Carousel } from "react-bootstrap";
import { SliderItem } from "./components";
import videos from "../../assets/videos";
function Cards(): JSX.Element {
  const onSlide = (eventName: string) => {
    const event = new Event(eventName);
    document.dispatchEvent(event);
  };

  const onClick = (event: MouseEvent) => {
    const target = event.currentTarget as HTMLDivElement;
    const parent = target.closest<HTMLDivElement>(
      ".collapseable"
    ) as HTMLDivElement;
    const materialIcon = parent.querySelector<HTMLSpanElement>(
      ".material-icons"
    ) as HTMLSpanElement;
    const child = parent.querySelector<HTMLDivElement>(
      ".collapse-child"
    ) as HTMLDivElement;
    if (child.classList.contains("collapse")) {
      child.classList.remove("collapse");
      parent.style.removeProperty("border-bottom");
      materialIcon.innerText = "expand_less";
      target.classList.add("mb-4");
      target.classList.remove("mb-0");
      parent.classList.remove("pb-0");
    } else {
      child.classList.add("collapse");
      parent.style.borderBottom = "0px";
      materialIcon.innerText = "expand_more";
      target.classList.remove("mb-4");
      target.classList.add("mb-0");
      parent.classList.add("pb-0");
    }
  };
  return (
    <>
      <div className="cards-main collapseable">
        <div className="row">
          <div className="col-lg-6 col-sm-12 pr-lg-5 pr-md-4">
            <h6
              className="content_sub_heading collapse-head mb-4 d-inline-flex justify-content-between align-items-center w-100"
              onClick={onClick}
            >
              <span>Awesome Similar Home</span>
              <span className="material-icons">expand_less</span>
            </h6>
          </div>
        </div>

        <div className="cards_slider collapse-child">
          <div className="card">
            <Carousel interval={null} onSlide={() => onSlide("sliderOne")}>
              <Carousel.Item>
                <SliderItem eventName="sliderOne" src={videos.SliderVideo1} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderOne" src={videos.SliderVideo2} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderOne" src={videos.SliderVideo3} />
              </Carousel.Item>
            </Carousel>

            <div className="card-body">
              <div className="cards-content">
                <div className="flat_price">
                  <p className="mb-0">$1,150,000</p>
                </div>
                <div className="flat-aspects">
                  <span className="flat_bed">
                    <img src={images.cardsBed} alt="beds" />
                  </span>
                  <p className="mb-0">3 Bed</p>
                  <span className="flat_bath">
                    <img src={images.flatBath} alt="bath" />
                  </span>
                  <p className="mb-0 pr-0">2 bath</p>
                </div>
              </div>
              <p className="flat_address mb-0">
                914 Edgemont Avenue Elk River, MN 55
              </p>
            </div>
          </div>
          <div className="card">
            <Carousel interval={null} onSlide={() => onSlide("sliderTwo")}>
              <Carousel.Item>
                <SliderItem eventName="sliderTwo" src={videos.SliderVideo1} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderTwo" src={videos.SliderVideo2} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderTwo" src={videos.SliderVideo3} />
              </Carousel.Item>
            </Carousel>
            <div className="card-body">
              <div className="cards-content">
                <div className="flat_price">
                  <p className="mb-0">$1,150,000</p>
                </div>
                <div className="flat-aspects">
                  <span className="flat_bed">
                    <img src={images.cardsBed} alt="beds" />
                  </span>
                  <p className="mb-0">3 Bed</p>
                  <span className="flat_bath">
                    <img src={images.flatBath} alt="bath" />
                  </span>
                  <p className="mb-0 pr-0">2 bath</p>
                </div>
              </div>
              <p className="flat_address mb-0">
                914 Edgemont Avenue Elk River, MN 55
              </p>
            </div>
          </div>
          <div className="card">
            <Carousel interval={null} onSlide={() => onSlide("sliderThree")}>
              <Carousel.Item>
                <SliderItem eventName="sliderThree" src={videos.SliderVideo1} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderThree" src={videos.SliderVideo2} />
              </Carousel.Item>
              <Carousel.Item>
                <SliderItem eventName="sliderThree" src={videos.SliderVideo3} />
              </Carousel.Item>
            </Carousel>
            <div className="card-body">
              <div className="cards-content">
                <div className="flat_price">
                  <p className="mb-0">$1,150,000</p>
                </div>
                <div className="flat-aspects">
                  <span className="flat_bed">
                    <img src={images.cardsBed} alt="beds" />
                  </span>
                  <p className="mb-0">3 Bed</p>
                  <span className="flat_bath">
                    <img src={images.flatBath} alt="bath" />
                  </span>
                  <p className="mb-0 pr-0">2 bath</p>
                </div>
              </div>
              <p className="flat_address mb-0">
                914 Edgemont Avenue Elk River, MN 55
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
export default Cards;
