import React from "react";
function Details(): JSX.Element{
    return (
        <div className="details">
            <h6 className=" mb-0 content_sub_heading pb-3"> Details</h6>
            <p className="mb-0">
                Luxurious sun-drenched newly renovated corner 1Bed / 1,5 Bath Condo, approx. 771 sq ft with a private
                terrace at the Maison East Condominium is located on one of the most desirable streets on the Upper East
                Side just four blocks away from the Central Park and Metropolitan Museum.
            </p>

        </div>

    )
}
export default Details;