import React, { createRef, useEffect, useState } from "react";
import images from "../../../../assets/images";

interface Props {
  src: string;
}

function SliderItem(props: Props): JSX.Element {
  const [play, setPlay] = useState(false);
  const [mute, setMute] = useState(false);
  const videoRef = createRef<HTMLVideoElement>();
  const timeRef = createRef<HTMLSpanElement>();
  useEffect(() => {
    document.addEventListener("mainSliderSlide", () => {
      if (videoRef.current) {
        const videoElement = videoRef.current as HTMLVideoElement;
        if (videoElement.played) {
          (videoRef.current as HTMLVideoElement).pause();
        }
      }
    });
    document.addEventListener("videoPlay", (event: any) => {
      if (event.detail !== "mainSliderSlide") {
        if (videoRef.current) {
          const videoElement = videoRef.current as HTMLVideoElement;
          if (videoElement.played) {
            videoElement.pause();
            videoElement.currentTime = 3;
          }
        }
      }
    });
  });
  return (
    <div className="slide-item">
      <video
        disablePictureInPicture={true}
        preload="metadata"
        ref={videoRef}
        onPlay={() => {
          setPlay(true);
          const event = new CustomEvent("videoPlay", {
            detail: "mainSliderSlide",
          });
          document.dispatchEvent(event);
        }}
        onPause={() => {
          setPlay(false);
          (videoRef.current as HTMLVideoElement).currentTime = 3;
        }}
        onTimeUpdate={(event) => {
          event.persist();
          const videoElement = videoRef.current as HTMLVideoElement;
          const currentTime = videoElement.currentTime;
          (timeRef.current as HTMLSpanElement).innerText = (
            currentTime / 100
          ).toFixed(2);
        }}
      >
        <source src={`${props.src}#t=5`} />
      </video>
      <img
        src={images.playButton}
        alt="play"
        className="play-button"
        onClick={() => {
          (videoRef.current as HTMLVideoElement).play();
        }}
        hidden={play}
      />
      <div className="top-right-actions">
        <img src={images.shareButton} alt="play" />
        <img src={images.likeButton} alt="play" />
      </div>
      <div className="bottom-right-actions">
        <span
          ref={timeRef}
          style={{ fontSize: "12px", fontWeight: "bold", color: "white" }}
        >
          00:00
        </span>
        <span
          className="volume-button"
          onClick={() => {
            const videoElement = videoRef.current as HTMLVideoElement;
            const muted = videoElement.muted;
            videoElement.muted = !muted;
            setMute(!muted);
          }}
        >
          <span className="material-icons">
            {mute ? "volume_off" : "volume_up"}
          </span>
        </span>
      </div>
    </div>
  );
}

export default SliderItem;
