import React from "react";
import images from "../../assets/images";
import videos from "../../assets/videos";
import { Carousel } from "react-bootstrap";
import { SliderItem } from "./components";

function Slider(): JSX.Element {
  const onSlide = () => {
    const event = new Event("mainSliderSlide");
    document.dispatchEvent(event);
  };
  return (
    <div className="slider-area">
      <div className="slider">
        <div className="left-slider-images mr-1">
          <div className="left-slider-top-image">
            <img src={images.sliderSurroundingImage1} alt="mainSliderLeftTop" />
          </div>
          <div className="left-slider-bottom-image">
            <img
              src={images.sliderSurroundingImage2}
              alt="mainSliderLeftBottom"
            />
          </div>
        </div>
        <div className="center-slider-carousel  mr-1">
          <Carousel
            interval={null}
            nextIcon={
              <div className="carousel-control-icon">
                <span className="material-icons">chevron_right</span>
              </div>
            }
            prevIcon={
              <div className="carousel-control-icon">
                <span className="material-icons">chevron_left</span>
              </div>
            }
            onSlide={onSlide}
          >
            <Carousel.Item>
              <SliderItem src={videos.SliderVideo1} />
            </Carousel.Item>
            <Carousel.Item>
              <SliderItem src={videos.SliderVideo2} />
            </Carousel.Item>
            <Carousel.Item>
              <SliderItem src={videos.SliderVideo3} />
            </Carousel.Item>
          </Carousel>
        </div>
        <div className="right-slider-images mr-1">
          <div className="right-slider-top-image">
            <img src={images.sliderSurroundingImage3} alt="mainSliderLeftTop" />
          </div>
          <div className="right-slider-bottom-image">
            <img
              src={images.sliderSurroundingImage4}
              alt="mainSliderLeftBottom"
            />
          </div>
        </div>
      </div>
    </div>
  );
}
export default Slider;
