import React, { createRef, MouseEvent, useState } from "react";
import moment from "moment";
import clsx from "clsx";
import images from "../../assets/images";

const datesArray = Array(10)
  .fill(1)
  .map((item, index) => {
    const date = moment().add(-4, "days");
    return date.add(item + index, "days").toISOString();
  });
const now = moment().toDate().toISOString();
function ScheduleTour(): JSX.Element {
  const [dates, setDates] = useState<string[]>(datesArray);
  const [active, setActive] = useState<string>(now);
  const calenderRef = createRef<HTMLDivElement>();
  const onClickSchedule = (event: MouseEvent<HTMLDivElement>) => {
    const target = event.currentTarget as HTMLDivElement;
    const data = target.dataset;
    if (data.hasOwnProperty("date")) {
      setActive(data.date as string);
    }
  };

  const scroll = () => {
    const scrollItem = calenderRef.current as HTMLDivElement;
    scrollItem.scrollBy(100, 0);
    const lastDate = dates[dates.length - 1];
    if (lastDate) {
      const newDates = Array(2)
        .fill(1)
        .map((item, index) => {
          return moment(lastDate)
            .add(item + index, "days")
            .toISOString();
        });

      setDates([...dates, ...newDates]);
    }
  };
  return (
    <div className="schedule_tour">
      <ul>
        <li>
          <span className="calender_icon">
            <img src={images.calender} alt="calender" />
          </span>
          <p className="calender_heading mb-0">Schedule a Tour</p>
        </li>
      </ul>
      <div className="calender-main">
        <div className="calender-scroll-container">
          <img
            src={images.calenderScroller}
            alt="scroller"
            className="scroller"
            onClick={scroll}
          />
          <div className="calender-scroll-body" ref={calenderRef}>
            <div className="calender-inner">
              {dates.map((date) => (
                <div
                  className={clsx(
                    "calender_date_days",
                    moment(active).format("YYYY-MM-DD") ===
                      moment(date).format("YYYY-MM-DD")
                      ? "active_schedule"
                      : ""
                  )}
                  onClick={onClickSchedule}
                  data-date={date}
                  key={date}
                >
                  <p className="day mb-0">{moment(date).format("ddd")}</p>
                  <p className="date mb-0">{moment(date).format("DD")}</p>
                </div>
              ))}
            </div>
          </div>
        </div>

        <div className="schedule_tour_buttons ">
          <button className="btn choose_time">Choose time</button>
          <button className="btn schedule_a_tour">Schedule a Tour</button>
        </div>
      </div>
    </div>
  );
}

export default ScheduleTour;
